.well {
  background: $well-color;
  border: solid 1px $well-border-color;
  padding: $the__global__padding;
  height: 100%;
  margin-bottom: $the__global__padding;
  @include radius;

  &--lg {
    @extend .well;
    padding: $the__global__padding * 2; } }
