.pagination {
	border: solid 1px $mute-border-color;
	background: #fff;
	@include radius;
	@include clearfix;
	padding: 0;
	margin: 0;
	display: inline-block;

	li {
		display: block;
		float: left;
		height: 40px;
		width: 40px;
		line-height: 40px;
		text-align: center;

		&.disable {
			background: $mute-color;
			color: darken($mute-border-color, 10%);
			cursor: no-drop; }

		a {
			display: block;
			color: $primary-color;
			text-decoration: none; }

		&:not(.disable).active,
		&:not(.disable):hover {
			background: $primary-color;
			a {
				color: #fff; } } } }

