.panel {
  position: relative;
  background: #fff;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
  @include radius;
  margin-top: $the__global__padding;



  h1, h2, h3, h4, h5, h6, p {
    padding: 0;
    margin: 0; }



  &--default {
    border: solid 1px $mute-border-color; }


  &__header,
  &__body,
  &__footer {
    padding: ($the__global__padding / 2) $the__global__padding; }

  &__header {
    border-bottom: solid 1px $mute-border-color;
    font-weight: 600; }


  &__body {
    flex: 1;
    p {
      padding-bottom: $the__global__padding; } }

  &__footer {
    background: $mute-color; } }
